import React from 'react';
import GridCell from './GridCell';
import {GridRowContainer} from './styles/GridRowContainer.styles';
import { determineCellState } from './utils/cellUtil';

const GridRow = ({word, rowSize, secretWord, isGameOverRow}) => {
    
    return (
        <GridRowContainer>
            {word.map((letter, index) => (<GridCell key={index}
                                                    letter={letter}
                                                    nbCells={rowSize}
                                                    state={determineCellState(
                                                                            secretWord,
                                                                            word,
                                                                            index,
                                                                            isGameOverRow)}/>))}
        </GridRowContainer>
    );
}

export default  React.memo(GridRow);