import React from 'react';
import {GridCellContainer} from './styles/GridCellContainer.styles';

const GridCell = ({letter, nbCells, state}) => {
    return (
        <GridCellContainer nbCells={nbCells} state={state}>
            {!letter ? "" : letter }
        </GridCellContainer>
    );
}

export default  React.memo(GridCell);