import React from 'react';
import {MenuContainer} from './styles/MenuContainer.styles.js';
import {Link} from './styles/Link.styles.js';
import {WordSizeContainer} from './styles/WordSizeContainer.styles.js'
import Dropdown from './Dropdown.js';

const Menu = ({newGame, setWordSize, defaultWordSize}) => {
    return (
        <MenuContainer>
            <Link href="#" onClick={newGame}>Nouveau jeu</Link>

            <WordSizeContainer>
                <label>Nombre de lettres</label>
                <Dropdown options={[
                                    { value: 6, label: '6' },
                                    { value: 7, label: '7' },
                                    { value: 8, label: '8' },
                                    { value: 9, label: '9' }]}
                        handleOnChange={setWordSize} 
                        defaultValue={defaultWordSize}/>
            </WordSizeContainer>
        </MenuContainer>
    );
}

export default  React.memo(Menu);