import styled from 'styled-components';

export const InputText = styled.input.attrs({ 
    type: 'text',
  })`
    background: #FFFFFF;
    border-radius: 5px;
    height: 2em;
    border-color: transparent;
    outline: none;
    transition: 0.15s;
    font-size:2em;
    font-weight:bold;
    text-align: center;
    text-transform: uppercase;
    color: #000000;
    maxlength: ${props => props.maxLength};
    
    ::placeholder {
      color: #DCDCDC;
      font-size: 1em;
    }

    `;