import React, {useCallback, useState, useMemo } from 'react';
import Game from './Game';
import Menu from './Menu';
import {AppContainer} from './styles/AppContainer.styles';
import { createGame, getWords, updateGame  } from './utils/gameUtil'

const App = () => {

  const defaultWordSize = 7;

  const [optionsWordSize, setOptionsWordSize] = useState(defaultWordSize);

  const words = useMemo(() => getWords(optionsWordSize), [optionsWordSize]);

  const [gameId, setGameId] = useState(1);

  const [games, setGames] = useState([createGame(gameId, words)]);

  const addNewGame = (newGameId, words) => setGames(games => [...games, createGame(newGameId, words)]);

  const getCurrentGame = gameId => games.find(game => game.id === gameId);

  const updateCurrentGame = (props) => setGames(games => updateGame(games, gameId, props));

  const newGame = useCallback(() => {
    // Increment Game id
    const newGameId = gameId + 1;
    setGameId(newGameId);

    // Add new game
    addNewGame(newGameId, words);

  }, [gameId, words]);

  console.log({optionsWordSize, gameId, currentGame: getCurrentGame(gameId), games})

  return (
    <AppContainer>
      <Menu newGame={newGame} setWordSize={setOptionsWordSize} defaultWordSize={defaultWordSize}/>
      <Game key={gameId} game={getCurrentGame(gameId)} updateGame={updateCurrentGame} />
    </AppContainer>
  );
}

export default React.memo(App);