import React, {useCallback, useState} from 'react';

import Grid from './Grid';
import gameState from './enums/gameState';
import {computeKnownLetters, updateGrid, isVictory, isGameOver, startVictoryAnimation} from './utils/gameUtil';
import {createEmptyRow, createFirstLetterOnlyRow} from './utils/cellUtil';
import {GameContainer} from './styles/GameContainer.styles';
import Textbox from './Textbox';

const Game = ({ game, updateGame }) => {

    // Declare the letters known by the player
    const [knownLetters, setKnownLetters] = useState(createFirstLetterOnlyRow(game.secretWord, game.secretWord.length));

    // Declare the grid
    const [grid, setGrid] = useState(
        [[...knownLetters],
        createEmptyRow(game.secretWord.length),
        createEmptyRow(game.secretWord.length),
        createEmptyRow(game.secretWord.length),
        createEmptyRow(game.secretWord.length),
        createEmptyRow(game.secretWord.length)]);

    // Declare the current row
    const [currentRowIndex, setCurrentRowIndex] = useState(0);

    // Actions following a key pressed by the player
    const onEnterKeyPress = useCallback((text) => {

        // Get the word inputted by the player and normalize it
        const word = text.toLowerCase();

        // Increment the current row
        const newCurrentRowIndex = currentRowIndex + 1;
        setCurrentRowIndex(newCurrentRowIndex);

        // Update the known letters
        const newKnownLetters =  computeKnownLetters(knownLetters, game.secretWord, word);
        setKnownLetters(newKnownLetters);

        // Check if Victory
        if(isVictory(newKnownLetters, game.secretWord)) {
            updateGame({ state: gameState.victory });
            startVictoryAnimation();

            // Update the last row
            setGrid(grid => updateGrid(
                grid,
                currentRowIndex,
                newCurrentRowIndex,
                createEmptyRow(game.secretWord),
                newKnownLetters));
        }
        // Check if Game Over
        else if(isGameOver(newCurrentRowIndex, grid)) {
            updateGame({ state: gameState.gameOver });

            // Update the last row
            setGrid(grid => updateGrid(
                grid,
                currentRowIndex,
                currentRowIndex,
                undefined,
                [...game.secretWord]));
        }
        else {
        // Update the two last rows
        setGrid(grid => updateGrid(
                                    grid,
                                    currentRowIndex,
                                    newCurrentRowIndex,
                                    newKnownLetters,
                                    [...word]));
        }
    }, [currentRowIndex, grid, game.secretWord]);

    console.log("game rendered", game)
    return (
        <GameContainer>
            <Textbox placeholder="Saisir un mot..." onEnterKeyPress={onEnterKeyPress} maxLength={game.secretWord.length} disabled={game.state !== gameState.inProgress}/>
            <Grid secretWord={game.secretWord} grid={grid} state={game.state} rowSize={game.secretWord.length}/>
        </GameContainer>
    );
}

export default React.memo(Game);