import React from 'react';
import GridRow from './GridRow';
import {GridContainer} from './styles/GridContainer.styles';
import gameState from './enums/gameState';

const Grid = ({ secretWord,  rowSize, grid, state }) => {

    const isGameOverRow = (rowIndex) => state === gameState.gameOver && (rowIndex + 1 === grid.length);
    return (
    <GridContainer>
        {grid.map((word, rowIndex) => (<GridRow
                                                key={rowIndex}
                                                word={word}
                                                rowSize={rowSize}
                                                secretWord={secretWord}
                                                isGameOverRow={isGameOverRow(rowIndex)} />))}
    </GridContainer>
    );
}

    export default React.memo(Grid);