import styled from 'styled-components';
import cellState from '../enums/cellState';

const getBackground = (state) => {
  switch(state) {
    case cellState.unknown:
      return "rgba(255, 255, 255, 0.12)";
    case cellState.wrongPosition:
      return "#fcc200";
    case cellState.rightPosition:
      return "#007f66";
      case cellState.gameOver:
        return "#DC143C";
    default:
      throw new Error(`Unknown state : ${state}`);
  }
}

export const GridCellContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-basis: 100%;
  margin:3px;
  text-transform: uppercase;
  height: ${props => 40 / props.nbCells}rem; 
  width: ${props => 40 / props.nbCells}rem;
  font-size:2em;  
  font-weight:bold;
  background-color: ${props => getBackground(props.state)};
  border-radius:2px;
`;