import React, {useState} from 'react';
import {InputText} from './styles/InputText.styles';

const Textbox = ({placeholder, onEnterKeyPress, maxLength, disabled}) => {

    const [text, setText] = useState("");

    const onChange =  (event) => setText(event.target.value);

    const onKeyPress = ({keyCode}) => {
        if (keyCode === 13 && maxLength === text.length) {
            onEnterKeyPress(text);
            setText("");
        }
    }

    return (
        <InputText
            value={text}
            maxLength={maxLength}
            placeholder={placeholder}
            onKeyDown={onKeyPress}
            onChange={onChange}
            disabled={disabled} />
    );
}

export default React.memo(Textbox);