import words from 'an-array-of-french-words';
import confetti from 'canvas-confetti';
import gameState from '../enums/gameState.js';

export const createGame = (gameId, words) => ({id: gameId, secretWord : getSecretWord(words), state: gameState.inProgress});

export const updateGame = (games, gameId, props) => games.map(game => game.id === gameId
                                                                ? ({...game, ...props})
                                                                : game);

export const getWords = (nbLetters) => {
    return words.filter(word => word.length === nbLetters)
                .map(word => word.normalize("NFD").replace(/[\u0300-\u036f]/g, ""));
}

export const computeKnownLetters = (knownLetters, secretWord, word) => {
    return knownLetters.map((letter, index) => {
        // Letter is already known
        if (letter !== null) {
            return letter;
        }
        // A new letter has been found
        else if(secretWord[index] === word[index]) {
            return word[index];
        }
        // Wrong letter
        else {
            return null;
        }
    });
}

export const updateGrid = (array, rowIndex, lastRowIndex, knownLetters, word) =>  {
    return array.map((row, index) => {
        if (index === rowIndex) {
            return word;
        }
        else if (index === lastRowIndex) {
            return knownLetters;
        }
        else {
            return row;
        }
    });
  }

export const isVictory = (knownLetters, secretWord) => knownLetters.every((letter, index) => letter === secretWord[index]);

export const isGameOver = (currentRowIndex, grid) => currentRowIndex  === grid.length;

export  const startVictoryAnimation = () => {
    const duration = 15 * 1000;
    const animationEnd = Date.now() + duration;
    const defaults = { startVelocity: 30, spread: 360, ticks: 60, zIndex: 0 };

    const randomInRange = (min, max) => Math.random() * (max - min) + min;

    const interval = setInterval(() => {
        const timeLeft = animationEnd - Date.now();

        if (timeLeft <= 0) {
            return clearInterval(interval);
        }

        const particleCount = 50 * (timeLeft / duration);
        // since particles fall down, start a bit higher than random
        confetti(Object.assign({}, defaults, { particleCount, origin: { x: randomInRange(0.1, 0.3), y: Math.random() - 0.2 } }));
        confetti(Object.assign({}, defaults, { particleCount, origin: { x: randomInRange(0.7, 0.9), y: Math.random() - 0.2 } }));
        }, 250);
}

const getSecretWord = (words) => {
    const word = words[Math.floor(Math.random()*words.length)];
    console.log("getSecretWord", {length: words.length, word});
    return word;
}