import cellState from '../enums/cellState';

export const determineCellState = (secretWord, word, index, isGameOverRow) => {

    // Display a special state if this cell is part of the game over row
    if(isGameOverRow) {
        return cellState.gameOver
    }
    // Determine the current letter in the cell
    const letter = word[index];

    // Check if the letter has the right position in the secret word
    if (letter === secretWord[index]) {
        return cellState.rightPosition;
    }

    // Check if the letter is part of the secret word but not in the right position
    if(secretWord.indexOf(letter) !== -1) {

        // Count the number of occurences of the current letter in the secret word
        const nbSecretWordLetterOccurences = [...secretWord].filter(l => l === letter).length;

        // Count the number of occurences of the current letter having the right position
        const nbRightPositionLetterOccurences = word.filter((l, i) => l === letter && secretWord[i] === letter).length;

        // Letter can be indicated as wrong position only if :
        // Number of letters occurences in right position + number of existing letters in wrong position < number of letters occurences in the secret word
        if (nbRightPositionLetterOccurences < nbSecretWordLetterOccurences) {

            // Count the number of occurences of the current letter having the wrong position and index < current letter index (check the existing letters, on the left)
            const nbWrongPositionLetterOccurences = word.filter((l, i) => i <  index && l === letter && secretWord[i] !== letter).length;

            if ((nbRightPositionLetterOccurences + nbWrongPositionLetterOccurences) < nbSecretWordLetterOccurences) {
                return cellState.wrongPosition; 
            }
        }         
    }

    return cellState.unknown;
}

export const createEmptyRow = (size) => new Array(size).fill(null);

export const createFirstLetterOnlyRow = (secretWord, rowSize) =>  [...secretWord[0], ...new Array(rowSize - 1).fill(null)]